.header {
  background-color: $primary-text-color;
  isolation: isolate;
}
.header__container {
  position: relative;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  // height: 230px;
  // width: 320px;
  padding-top: 40px;
  padding-bottom: 60px;
  font-weight: 500;
  line-height: 1.17;

  &-library {
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
    height: 230px;
    width: 320px;
    margin: 0 auto;
    padding-top: 46px;
    font-weight: 500;
    line-height: 1.17;
  }
}
.is-hidden {
  visibility: hidden;
  pointer-events: none;
  height: 42px;
}
.logo {
  display: flex;
  &__img {
    width: 25px;
    height: 35px;
  }
}
.site-name {
  font-size: 0px;
  margin-left: auto;
}
.nav {
  display: flex;
  margin: 0 auto;
  width: 278px;
  font-size: $primary-font-size;
  font-weight: 500;
  line-height: 1.17;
  color: $lighter-text-color;
}
.menu__container {
  margin-left: auto;
  align-self: flex-end;
  height: 18px;
}
.nav-list {
  align-items: center;
  display: flex;
  justify-content: space-between;
  position: relative;

  li + li {
    margin-left: 15px;
  }

  &__link-home-accent:hover {
    color: $accent-color;
    transition-duration: 0.5s;
  }
  &__link-library-accent:hover {
    color: $accent-color;
    transition-duration: 0.5s;
  }
  &__link-sign-in-accent:hover {
    color: $accent-color;
    transition-duration: 0.5s;
  }

  &__item-library-active::after {
    content: '';
    display: flex;
    position: absolute;
    bottom: 3;
    width: 69px;
    height: 3px;
    background-color: $accent-color;
    color: $lighter-text-color;
  }

  &__item-home-active::after {
    content: '';
    display: flex;
    position: absolute;
    bottom: 3;
    width: 34px;
    height: 3px;
    background-color: $accent-color;
  }
}

.sign-in__button {
  display: flex;
  align-items: center;
  cursor: pointer;
  background-color: $transparent;
  border: none;
  color: $lighter-text-color;
  padding: 0;
}

.google__sign-in{
  display: block;
  margin-left: 2px;
  width: 20px;
  height: 20px;
  background-image:
      url('../images/logo-google-mini.png');
}

.display-name {
  margin-right: 10px;
}
.nav-list__item-log-in {
    display: flex;
}
.search {
  display: flex;
  align-items: flex-end;
  height: 53px;
  font-size: 14px;
  &__form {
    display: flex;
    width: 280px;
    height: 20px;
    margin: 0 auto;
    border-bottom: 0.2px solid #f7f7f7bd;
  }
  &__form:hover,
  &__form:focus {
    border-bottom: 0.2px solid $accent-color;
  }
  &__input {
    border: none;
    outline: none;
    color: $lighter-text-color;
    background: $transparent;
    width: 100%;
  }
  &__img {
    width: 15px;
    height: 15px;
    cursor: pointer;
  }
  &__img:hover {
    width: 18px;
    height: 18px;
  }
  &__btn {
    background-color: $transparent;
    border: none;
    width: 20px;
    outline: none;
  }
}

::-webkit-input-placeholder {
  color: $lighter-text-color;
}
:-moz-placeholder {
  color: $lighter-text-color;
}
::-moz-placeholder {
  color: $lighter-text-color;
}
:-ms-input-placeholder {
  color: $lighter-text-color;
}

