// todo ======= mobile img === start
@media screen and (max-width: 767px) {
  .header__container {
    background-image: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url('../images/header-bg/header-bkg-mobile-1@1x.png');

    &-library {
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-bg/header-bkg-mobile-1@1x.png');
    }
  }
}
@media screen and (min-device-pixel-ratio: 2) and (max-width: 767px),
  (min-resolution: 192dpi) and (max-widtn: 767px),
  (min-resolution: 2dppx) and (max-widtn: 767px) {
  .header__container {
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/header-bg/header-bkg-mobile-1@2x.png');

    &-library {
      background-image: linear-gradient(
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url('../images/header-bg/header-bkg-mobile-1@2x.png');
    }
  }
}

